import { Injectable } from '@angular/core';
import { CommentsComponent } from '../components/comments/comments.component';

@Injectable({
  providedIn: 'root'
})
export class ChatService {

  public FB : any;
  public isVisible = false;
  public isInit = false;

  constructor() {
    console.log('Chat Service Ok')
   }

  init(show = false){


    let script = document.createElement("script");
    script.type = "text/javascript";
//    script.src = "https://connect.facebook.net/es_LA/sdk.js";
    //script.src = "https://connect.facebook.net/en_US/sdk/debug.js";
    script.src = "https://connect.facebook.net/es_LA/sdk/xfbml.customerchat.js";
    script.onload = () => {
      if(window['FB']){
        this.FB = window['FB'];
        console.log('FB init');
        this.FB.init({ 
          appId: '140247018227426',
          status: true, 
          cookie: true, 
          xfbml: true,
          autoLogAppEvents : true,
          version: 'v2.4'
        });

        //this.FB.CustomerChat.showDialog();
        this.FB.Event.subscribe('customerchat.load', ()=>{
          if(!show)
            this.hide();
          this.isInit = true;
        });

        this.isVisible = show;
        
      }else{
        console.error('Falta plugin FB')
      }

    }

    document.body.appendChild(script);

  }

  show(){
    this.checkInit();
    console.log('FB show');
    

    var content = document.getElementById('fb-root');

    if(content){
      this.isVisible = true;
      content.style.display = "block"; 
    }else{
      console.error('Not Found element #fb-root');
      //this.FB.XFBML.parse();
    }

  }

  hide(){
    console.log('FB hide');
      
    var content = document.getElementById('fb-root');

    if(content){
      this.isVisible = false;
      content.style.display = "none"; 
    }else{
      console.error('Not Found element #fb-root');
    }
  }

  checkInit(){
    if(! this.FB){
      this.init(true);
    }
  }

  toggle(){
    if(this.isVisible){
      this.hide();
    }else{
      this.show();
    }
  }

}
