

import { Plugins, CameraResultType, Capacitor, FilesystemDirectory, 
    CameraPhoto, CameraSource } from '@capacitor/core';
import { Model } from './model';
import { RequestService } from '../request.service';
import { Crop, CropOptions } from '@ionic-native/crop';
import { ModalController } from '@ionic/angular';
import { CropPageModule } from 'src/app/pages/crop/crop.module';
import { CropPage } from 'src/app/pages/crop/crop.page';
import { element } from 'protractor';
import { Component } from '@angular/core';



const { Camera, Filesystem, Storage } = Plugins;


export class ModelImage {

    public photos: Photo[] = [];

    public photo : any;
    public url :string ;
    public thumb : boolean ;
    public serviceImage : Model;
    public image : any ;
    public path : string;
    aspectRatio : any = '1 / 1';



    element_img = null;
    MAX_WIDTH = 1000;


    cropOptions : CropOptions = {
      quality: 50
    }

    constructor(
      request : RequestService,
      public modalController: ModalController
      ) { 
        this.serviceImage = new Model('Image',request);
    }
  
    public init(url : string, thumb : boolean = false ,pathDefault = 'assets/images/pngwing.png'){
        this.url = url ?? '/images/';
        this.thumb = thumb ;
        this.path = pathDefault;
        this.photo = new Photo(pathDefault);
    }
  
    setImage(image){
      this.image = image;
      this.photo.image = image;
    }


    setElementImg(element,max_width = 1000){         
      this.element_img = element;
      this.MAX_WIDTH = max_width;
    }
  
    public async addNewToGallery() {
      // Take a photo
      const capturedPhoto = await Camera.getPhoto({
        resultType: CameraResultType.Uri, 
        source: CameraSource.Camera, 
        quality: 100 ,
        allowEditing : true,
        correctOrientation : false
      });
  
  
  
      const base64Data = await this.readAsBase64(capturedPhoto);
  
    
      this.photos.unshift(new Photo(null,capturedPhoto.webPath, base64Data, this.url, this.thumb));
    }
  
  
    public async addOneNewToGallery() {

      //this.testCompres();
      // Take a photo
      const capturedPhoto = await Camera.getPhoto({
        resultType: CameraResultType.Base64, 
        source: CameraSource.Camera, 
        quality: 30 ,
        correctOrientation : false 
      });
    
      const base64Data = "data:image/jpeg;base64," +  capturedPhoto.base64String; //await this.readAsBase64(capturedPhoto);

      //console.log('base64 canvas' , this.thumbnail(base64Data , 700 )); 
  
      this.photo = new Photo(capturedPhoto.webPath,base64Data, base64Data, this.url, this.thumb, this.image);
      this.image = this.photo;
      
      
      let crop = this.aspectRatio != 'none';
      this.testCompres(crop);
      
  

      console.log('photo',this.photo);
    }


    testCompres(crop = false){

      setTimeout( () => {
        console.log('compress')

        let e :any  = document.getElementById(this.element_img)

        if(e){

          //this.element_img.onload = function(e : any){

            const canvas = document.createElement('canvas');
            //this.MAX_WIDTH = 1000;

            if(this.MAX_WIDTH > e.naturalWidth)
            this.MAX_WIDTH = e.naturalWidth

            const scaleSize = this.MAX_WIDTH / e.width;

            canvas.width = this.MAX_WIDTH;
            canvas.height = e.height * scaleSize;

            const ctx = canvas.getContext("2d");

            ctx.drawImage(e ,0 , 0, canvas.width , canvas.height);

            let newBase64 = canvas.toDataURL();

            //this.photo.data = newBase64;
            //this.photo.webPath = newBase64;
            this.photo.setData(newBase64);


          //}
        }else{
          console.log('testCompres No load view')
        }

        if(crop){
          this.cropImage();
        }

      },100)


    }

   

    public setAspectRatioCrop(aspectRatio){
      this.aspectRatio = aspectRatio;
    }


    onResize($event){
      console.log($event)
    }
    async cropImage(){
        console.log('crop');
        const modal = await this.modalController.create({
          component: CropPage ,
          componentProps: { 
            base64: this.photo.data,
            aspectRatio : this.aspectRatio
          
          }
        });
        let self = this;
        modal.onDidDismiss().then(data=>{
          if(data['data'] && data['data']['base64']){
            self.photo.setData(data['data']['base64'] );
            self.testCompres();
          }
        })
    
        return await modal.present();
    }
  
    public isLoadPthoto() {
      return  this.photo && this.photo.data  ? true : false;
    }

    public hasImage(){
      return this.image && this.image.id ? true : false;
    }

    public getImageUpload(){
      let img = {...this.image}
      img.webviewPath = '';
      return img;
    }
  
    private async readAsBase64(cameraPhoto: CameraPhoto) {
      // Fetch the photo, read as a blob, then convert to base64 format
      const response = await fetch(cameraPhoto.webPath!);
      const blob = await response.blob();
    
      return await this.convertBlobToBase64(blob) as string;  
    }
    
    convertBlobToBase64 = (blob: Blob) => new Promise((resolve, reject) => {
      const reader = new FileReader;
      reader.onerror = reject;
      reader.onload = () => {
          resolve(reader.result);
      };
      reader.readAsDataURL(blob);
    });
  
    uploadImage(){
      if(this.isLoadPthoto){
        var photo = {...this.photo}
        photo.webviewPath = '';
        if(this.photo.id){
          return this.serviceImage.api_update(photo);
        }else{
          return this.serviceImage.api_create(photo);
        }
      }
    }

}


export class Photo {
    id? : string;
    filepath: string;
    public webviewPath: string;
    public data :string;
    url : string;
    thumb : boolean;
    image ? :{}
  
    constructor(  filepath : string = "assets/images/pngwing.png",webviewPath : string = null,data :string = '',url : string = '',thumb : boolean = false , image : any = null){
      this.id = image ? image.id : '';
      this.webviewPath = webviewPath;
      this.filepath = filepath;
      this.data = data;
      this.url = url;
      this.thumb = thumb;
      this.image = image
    }
  
    getPath(event = null){

      if(event)
        console.log(event);  
      //this.testCompres(event);
      

      if(this.webviewPath){
        return this.webviewPath;
      }
      else if(this.image && this.image['urlComplete'] ){
        return this.image['urlComplete'];
      }else{
        return this.filepath;
      }
    }

    
    setData(data){
      this.webviewPath = data;
      this.data = data;
    }

  }
  
