import { Component } from '@angular/core';

import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { AuthUserService } from './services/auth-user.service';
import { SwUpdate } from '@angular/service-worker';
import { DialogService } from './api/util/dialog.service';
import { ConversationsService } from './services/conversations.service';


@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent {


  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private authUser : AuthUserService,
    private swUpdate: SwUpdate,
    private dialogService : DialogService,
    //public conversation : ConversationsService
  ) {
    this.initializeApp();
  }

  async initializeApp() {
    this.platform.ready().then(async () =>  {
      console.log('init APP')
      await this.authUser.getUser();
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });

    if (this.swUpdate.available) {
      this.swUpdate.available.subscribe(() => {
        //if (confirm('Hay una nueva version disponible, desea cargarla ?'))
        this.dialogService.showToast('Actualizando a nueva versión...','','info');
        window.location.reload();
      });
    }

    

  }
}
