import { ErrorHandler, Injectable } from '@angular/core';
import { RequestService } from '../api/request.service';
import { Model } from '../api/models/model';
import { DialogService } from '../api/util/dialog.service';
import { ToastController } from '@ionic/angular';
import { Router } from '@angular/router';


@Injectable()
export class MyErrorHandler implements ErrorHandler {
    
    private modelError : Model

    private lastError :null;

    constructor(
        private requestService : RequestService,
        private utilDialog : DialogService,
        private toastCtr : ToastController,
        private router : Router

    ){
        this.modelError = new Model('Error',requestService);

    }

    handleError(error: any): void {


        console.error('error locas',error);

        var caller_line = error.stack.split("\n");
        
        if(caller_line[2]){
            caller_line = caller_line[2];
        }else{
            caller_line = '';
        }

        let text = error.message

        if(this.lastError != text){
            console.log(error.status)
            this.lastError = text;
            var e = {
                text : text,
                url : window.location.href,
                line : caller_line
            }
    
            this.modelError.api_create(e).subscribe(
                request => {
                    //console.log(request);
                }
            );
            

            //this.showMsj();
           
        }

        

        //console.warn(error);
        //console.error('error lucas',error);
        //throw new Error("Method not implemented.");
    }

    async showMsj(){
        const toast = await this.toastCtr.create({
            header : 'Error', 
            color :'danger',
            message: "Error en tiempo de ejecución, pronto el administrador estara resolviendo este inconvente. Por favor intente nuevamente mas tarde",
            //duration:9999999,
            buttons: [
                {
                  text: 'Aceptar',
                  role: 'cancel',
                  handler: () => {
                    console.log('Cancel clicked');
                    //this.router.navigateByUrl('/')
                    window.location.assign('/');
                  }
                }
              ]
          });
          toast.present();
    }

}
